import { CustomerInfo } from "./customer-info"

const mapping: Partial<
  Record<
    keyof CustomerInfo | 'info',
    (lambdaParameter: string, value: string | number) => string
  >
> = {
  firstname: (lambdaParameter, value) => `${(lambdaParameter)}.Firstname.Contains("${value}")`,
  lastname: (lambdaParameter, value) => `${(lambdaParameter)}.Lastname.Contains("${value}")`,
  pesel: (lambdaParameter, value) => `${(lambdaParameter)}.Pesel.Contains("${value}")`,
  documentNumber: (lambdaParameter, value) => `${(lambdaParameter)}.DocumentNumber.Contains("${value}")`,
  info: (lambdaParameter, value) => `${value.toString().split(" ").map((q) =>
    `(${(lambdaParameter)}.Firstname+${(lambdaParameter)}.Lastname+${(lambdaParameter)}.Pesel+${(lambdaParameter)}.DocumentNumber).Contains("${q}")`).join("&&")}`,
}

export const getFilterMapper = (key: keyof CustomerInfo) =>
  mapping[key] as (lambdaParameter: string, value: string | number) => string
