import { ObjectDto } from "../dto/object.dto"

const mapping: Partial<
  Record<
    keyof ObjectDto,
    (lambdaParameter: string, value: string | number) => string
  >
> = {
  name: (lambdaParameter, value) => `${(lambdaParameter)}.Name.Contains("${value}")`
}

export const getFilterMapper = (key: keyof ObjectDto) =>
  mapping[key] as (lambdaParameter: string, value: string | number) => string
