import {
  Button, ButtonGroup, createStyles, Dialog,
  DialogActions,
  DialogContent,
  DialogTitle, makeStyles, MenuItem, Select, TextField, Theme
} from '@material-ui/core'
import React, { useEffect, useState } from 'react'
import {
  sanitizeFetchType, useMutation,
  useRefresh, useShowContext, useTranslate
} from 'react-admin'
import useStateRef from 'react-usestateref'
import { EngineDto } from '../../../core/dto/device/engine/engine.dto'
import { StanbyMode } from '../../../core/enum/StanbyMode'
import { ResourceName } from '../../../core/ResourceName'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    dialogProperty: {
      marginBottom: theme.spacing(1)
    }
  })
)

export const ConfirmDialog = ({
  confirm,
  cancel,
  source,
  open,
  action,
}: {
  confirm: () => void
  cancel: () => void
  source: 'setStandbyMode' | 'lock' | 'setTSCanCredentials'
  open: boolean
  action: 'set' | 'remove'
}) => {
  const translate = useTranslate()
  return (
    <Dialog open={open}>
      <DialogTitle>
        {translate(`resources.engines.dialogs.confirm.${source}.${action}`)}
      </DialogTitle>
      <DialogActions>
        <Button variant="contained" onClick={confirm}>
          {translate('ra.action.save')}
        </Button>
        <Button variant="contained" onClick={cancel}>
          {translate('const.cancel')}
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export const SetTSCanCredentialsModal = ({
  open,
  close,
}: {
  open: boolean
  close: () => void
}) => {
  const classes = useStyles()
  const [loginValue, setLoginValue] = useState('')
  const [loginError, setLoginError, loginErrorCurrentValue] = useStateRef(false)
  const [loginErrorText, setLoginErrorText] = useState('')
  const [passwordValue, setPasswordValue] = useState('')
  const [passwordError, setPasswordError, passwordErrorCurrentValue] = useStateRef(false)
  const [passwordErrorText, setPasswordErrorText] = useState('')
  const [confirmOpen, setConfirmOpen] = useState(false)
  const { record: engine } = useShowContext<EngineDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleClose = () => {
    setLoginValue('')
    setPasswordValue('')
    setLoginError(false)
    setPasswordError(false)
    setLoginErrorText('')
    setPasswordErrorText('')
    close()
  }

  const handleSetTSCanCredentials = async () => {
    setConfirmOpen(false)
    setLoginError(false)
    setPasswordError(false)
    setLoginErrorText('')
    setPasswordErrorText('')
    if (loginValue.replace(/\s/g, '').length === 0) {
      setLoginError(true)
      setLoginErrorText(translate('resources.engines.dialogs.setTSCanCredentials.login.error-bad-string'))
    } else if (loginValue.replace(/\s/g, '').length !== loginValue.length) {
      setLoginError(true)
      setLoginErrorText(translate('resources.engines.dialogs.setTSCanCredentials.login.error-no-spaces'))
    }
    if (passwordValue.replace(/\s/g, '').length === 0) {
      setPasswordError(true)
      setPasswordErrorText(translate('resources.engines.dialogs.setTSCanCredentials.password.error-bad-string'))
    } else if (passwordValue.replace(/\s/g, '').length !== passwordValue.length) {
      setPasswordError(true)
      setPasswordErrorText(translate('resources.engines.dialogs.setTSCanCredentials.password.error-no-spaces'))
    }

    if (!loginErrorCurrentValue.current && !passwordErrorCurrentValue.current) {
      await mutate(
        {
          type: sanitizeFetchType('setTSCanCredentials'),
          resource: ResourceName.ENGINES,
          payload: {
            engineId: engine?.id,
            login: loginValue,
            password: passwordValue
          },
        },
        { returnPromise: true },
      )
      refresh()
      handleClose()
    }
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <ConfirmDialog
        open={confirmOpen}
        cancel={() => setConfirmOpen(false)}
        confirm={handleSetTSCanCredentials}
        source="setTSCanCredentials"
        action="set"
      />
      <DialogTitle>
        {translate('resources.engines.dialogs.setTSCanCredentials.title')}
      </DialogTitle>
      <DialogContent>
        <TextField
          className={classes.dialogProperty}
          label={translate('resources.engines.dialogs.setTSCanCredentials.login.label')}
          variant="outlined"
          value={loginValue}
          onChange={(e) => setLoginValue(e.target.value)}
          fullWidth
          error={loginError}
          helperText={loginErrorText}
        />
        <TextField
          className={classes.dialogProperty}
          label={translate('resources.engines.dialogs.setTSCanCredentials.password.label')}
          variant="outlined"
          value={passwordValue}
          onChange={(e) => setPasswordValue(e.target.value)}
          fullWidth
          type='password'
          autoComplete="new-password"
          error={passwordError}
          helperText={passwordErrorText}
        />
      </DialogContent>
      <DialogActions>
        <ButtonGroup>
          <Button variant="contained" onClick={() => setConfirmOpen(true)}>
            {translate('ra.action.save')}
          </Button>
          <Button variant="contained" onClick={handleClose}>
            {translate('const.cancel')}
          </Button>
        </ButtonGroup>
      </DialogActions>
    </Dialog>
  )
}

export const SetStandbyModeModal = ({
  open,
  close,
}: {
  open: boolean
  close: () => void
}) => {
  const classes = useStyles()
  const [value, setValue] = useState('')
  const [confirmOpen, setConfirmOpen] = useState(false)
  const { record: engine } = useShowContext<EngineDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleClose = () => {
    setValue('')
    close()
  }

  const handleSetStandbyMode = async () => {
    setConfirmOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('setStandbyMode'),
        resource: ResourceName.ENGINES,
        payload: {
          engineId: engine?.id,
          stanbyMode: value,
        },
      },
      { returnPromise: true },
    )
    setValue('')
    refresh()
    close()
  }

  useEffect(() => {
    if (open && engine?.stanbyMode) {
      setValue(engine?.stanbyMode as string)
    }
  }, [open, engine?.stanbyMode])

  return (
    <Dialog open={open} onClose={handleClose}>
      <ConfirmDialog
        open={confirmOpen}
        cancel={() => setConfirmOpen(false)}
        confirm={handleSetStandbyMode}
        source="setStandbyMode"
        action="set"
      />
      <DialogTitle>
        {translate('resources.engines.dialogs.setStandbyMode.title')}
      </DialogTitle>
      <DialogContent>
        <Select
          className={classes.dialogProperty}
          variant="outlined"
          value={value}
          onChange={(e) => setValue(e.target.value as string)}
          fullWidth>
          <MenuItem value={StanbyMode.ALWAYS_ON}>{translate(`resources.enums.standbyMode.${StanbyMode.ALWAYS_ON}`)}</MenuItem>
          <MenuItem value={StanbyMode.ALWAYS_OFF}>{translate(`resources.enums.standbyMode.${StanbyMode.ALWAYS_OFF}`)}</MenuItem>
          <MenuItem value={StanbyMode.SENSOR}>{translate(`resources.enums.standbyMode.${StanbyMode.SENSOR}`)}</MenuItem>
        </Select>
      </DialogContent>
      <DialogActions>
        <ButtonGroup>
          <Button variant="contained" onClick={() => setConfirmOpen(true)}>
            {translate('ra.action.save')}
          </Button>
          <Button variant="contained" onClick={handleClose}>
            {translate('const.cancel')}
          </Button>
        </ButtonGroup>
      </DialogActions>
    </Dialog>
  )
}

export const LockModal = ({
  open,
  close,
}: {
  open: boolean
  close: () => void
}) => {
  const classes = useStyles()
  const [value, setValue] = useState('')
  const [error, setError] = useState(false)
  const [errorText, setErrorText] = useState('')
  const [confirmOpen, setConfirmOpen] = useState(false)
  const { record: engine } = useShowContext<EngineDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleClose = () => {
    setValue('')
    setError(false)
    setErrorText('')
    close()
  }

  const handleLock = async () => {
    setConfirmOpen(false)
    if (value.replace(/\s/g, '').length === 0) {
      setError(true)
      setErrorText(translate('resources.engines.dialogs.lock.error-bad-string'))
    } else if (value.replace(/\s/g, '').length !== value.length) {
      setError(true)
      setErrorText(translate('resources.engines.dialogs.lock.error-no-spaces'))
    } else {
      await mutate(
        {
          type: sanitizeFetchType('lock'),
          resource: ResourceName.ENGINES,
          payload: {
            engineId: engine?.id,
            lockName: value,
          },
        },
        { returnPromise: true },
      )
      handleClose()
      refresh()
    }
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <ConfirmDialog
        open={confirmOpen}
        cancel={() => setConfirmOpen(false)}
        confirm={handleLock}
        source="lock"
        action="set"
      />
      <DialogTitle>
        {translate('resources.engines.dialogs.lock.title')}
      </DialogTitle>
      <DialogContent>
        <TextField
          className={classes.dialogProperty}
          variant="outlined"
          value={value}
          onChange={(e) => setValue(e.target.value)}
          fullWidth
          error={error}
          helperText={errorText}
        />
      </DialogContent>
      <DialogActions>
        <ButtonGroup>
          <Button variant="contained" onClick={() => setConfirmOpen(true)}>
            {translate('ra.action.save')}
          </Button>
          <Button variant="contained" onClick={handleClose}>
            {translate('const.cancel')}
          </Button>
        </ButtonGroup>
      </DialogActions>
    </Dialog>
  )
}
