import { DataProvider, GetListResult, GetOneParams, GetOneResult, Identifier, UpdateParams, UpdateResult } from "react-admin"
import { ENGINES_URL } from "../../api-urls"
import { CacheableDataProviderExtensionResult, DataProviderExtensionResult } from "../../common/data-provider"
import { DateTime } from "../../common/date-time"
import { get, getWithPagination, patch, put } from "../../common/fetch.utils"
import { EngineEntrancesDto } from "../../dto/device/engine/engine-entrances.dto"
import { EngineEventsDto } from "../../dto/device/engine/engine-events.dto"
import { EngineDto } from "../../dto/device/engine/engine.dto"
import { RemoveTSCanCredentialsDto } from "../../dto/device/engine/remove-tscan-credentials.dto"
import { SetStandbyModeDto } from "../../dto/device/engine/set-standby-mode.dto"
import { SetTSCanCredentialsDto } from "../../dto/device/engine/set-tscan-credentials.dto"
import { EngineLockResults } from "../../enum/EngineLockResults"
import { EngineUnlockResults } from "../../enum/EngineUnlockResults"
import { getFilterMapper } from "./engine-filter.mapper"
import { EngineListParams } from "./engine-list.params"
import { mapSortEngineParam } from "./engine-sort.mapper"

const getFilterParams = (
  partialValues: Partial<EngineDto>
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof EngineDto)
      return mapper('o', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    {
      filter: { ...filter },
      sort: { field, order },
      pagination: { page, perPage }
    }: EngineListParams
  ): Promise<GetListResult<EngineDto>> => {

    const filterParams = `o=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    queryParams.push(`sortBy=${mapSortEngineParam(field)}`)
    queryParams.push(`sortOrder=${order}`)
    queryParams.push(`rangeFrom=${page * perPage - perPage}`)
    queryParams.push(`rangeTo=${page * perPage - 1}`)
    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const { data, range: { total } } = await getWithPagination<EngineDto[]>(`${ENGINES_URL}/GetByConditions`, path)

    return Promise.resolve({
      data,
      total
    })
  },
  getOne: async (
    resource: string,
    { id }: GetOneParams
  ): Promise<GetOneResult<EngineDto>> => {
    const data = await get<EngineDto>(ENGINES_URL, `/${id}`)
    return Promise.resolve({
      data
    })
  },
  update: async (
    resource: string,
    { id, data, previousData }: UpdateParams
  ): Promise<UpdateResult> => {
    const update = await patch<{ id: Identifier, [n: string]: any }, EngineDto>(ENGINES_URL, {
      id,
      name: data.name || previousData.name,
      isWatched: data.isWatched,
      lastRefresh: new Date(new Date(data.lastRefresh).getTime() - (new Date(data.lastRefresh).getTimezoneOffset() * 60000)).toJSON() as DateTime || previousData.lastRefresh,
      address: data.address || previousData.address,
      direction: data.direction || previousData.direction,
      categoryId: data.categoryId || previousData.categoryId,
      onlineInfixCeck: data.onlineInfixCeck,
      multiplicity: data.multiplicity || previousData.multiplicity
    })
    return Promise.resolve({ data: update })
  },
  attachEvents: async (
    resource: string,
    params: EngineEventsDto
  ): Promise<CacheableDataProviderExtensionResult<EngineEventsDto>> => {
    const engineEvents = await put<EngineEventsDto, EngineEventsDto>
      (`${ENGINES_URL}/AttachEvents`, params)
    return {
      data: engineEvents
    }
  },
  detachEvents: async (
    resource: string,
    params: EngineEventsDto
  ): Promise<CacheableDataProviderExtensionResult<EngineEventsDto>> => {
    const engineEvents = await put<EngineEventsDto, EngineEventsDto>
      (`${ENGINES_URL}/DetachEvents`, params)
    return {
      data: engineEvents
    }
  },
  attachEntrances: async (
    resource: string,
    params: EngineEntrancesDto
  ): Promise<CacheableDataProviderExtensionResult<EngineEntrancesDto>> => {
    const engineEntrances = await put<EngineEntrancesDto, EngineEntrancesDto>
      (`${ENGINES_URL}/AttachEntrances`, params)
    return {
      data: engineEntrances
    }
  },
  detachEntrances: async (
    resource: string,
    params: EngineEntrancesDto
  ): Promise<CacheableDataProviderExtensionResult<EngineEntrancesDto>> => {
    const engineEntrances = await put<EngineEntrancesDto, EngineEntrancesDto>
      (`${ENGINES_URL}/DetachEntrances`, params)
    return {
      data: engineEntrances
    }
  },
  setTSCanCredentials: async (
    resource: string,
    params: SetTSCanCredentialsDto
  ): Promise<DataProviderExtensionResult<SetTSCanCredentialsDto>> => {
    const setTSCanCredentials = await put<SetTSCanCredentialsDto, SetTSCanCredentialsDto>
      (`${ENGINES_URL}/SetTSCanCredentials`, params)
    return {
      data: setTSCanCredentials
    }
  },
  removeTSCanCredentials: async (
    resource: string,
    params: RemoveTSCanCredentialsDto
  ): Promise<DataProviderExtensionResult<RemoveTSCanCredentialsDto>> => {
    const removeTSCanCredentials = await put<RemoveTSCanCredentialsDto, RemoveTSCanCredentialsDto>
      (`${ENGINES_URL}/RemoveTSCanCredentials`, params)
    return {
      data: removeTSCanCredentials
    }
  },
  lock: async (
    resource: string,
    params: LockParams
  ): Promise<DataProviderExtensionResult<LockResponse>> => {
    const lock = await put<LockParams, LockResponse>
      (`${ENGINES_URL}/Lock`, params)
    return {
      data: lock
    }
  },
  unlock: async (
    resource: string,
    params: UnlockParams
  ): Promise<DataProviderExtensionResult<UnlockResponse>> => {
    const unlock = await put<UnlockParams, UnlockResponse>
      (`${ENGINES_URL}/Unlock`, params)
    return {
      data: unlock
    }
  },
  setStandbyMode: async (
    resource: string,
    params: SetStandbyModeDto
  ): Promise<DataProviderExtensionResult<SetStandbyModeDto>> => {
    const setStandbyMode = await put<SetStandbyModeDto, SetStandbyModeDto>
      (`${ENGINES_URL}/SetStandbyMode`, params)
    return {
      data: setStandbyMode
    }
  }
} as EngineDataProvider

interface EngineDataProvider extends DataProvider {
  attachEvents: (
    resource: string,
    params: EngineEventsDto
  ) => Promise<CacheableDataProviderExtensionResult<EngineEventsDto>>;

  detachEvents: (
    resource: string,
    params: EngineEventsDto
  ) => Promise<CacheableDataProviderExtensionResult<EngineEventsDto>>;

  attachEntrances: (
    resource: string,
    params: EngineEntrancesDto
  ) => Promise<CacheableDataProviderExtensionResult<EngineEntrancesDto>>;

  detachEntrances: (
    resource: string,
    params: EngineEntrancesDto
  ) => Promise<CacheableDataProviderExtensionResult<EngineEntrancesDto>>;

  setTSCanCredentials: (
    resource: string,
    params: SetTSCanCredentialsDto
  ) => Promise<DataProviderExtensionResult<SetTSCanCredentialsDto>>;

  removeTSCanCredentials: (
    resource: string,
    params: RemoveTSCanCredentialsDto
  ) => Promise<DataProviderExtensionResult<RemoveTSCanCredentialsDto>>;

  lock: (
    resource: string,
    params: LockParams
  ) => Promise<DataProviderExtensionResult<LockResponse>>;

  unlock: (
    resource: string,
    params: UnlockParams
  ) => Promise<DataProviderExtensionResult<UnlockResponse>>;

  setStandbyMode: (
    resource: string,
    params: SetStandbyModeDto
  ) => Promise<DataProviderExtensionResult<SetStandbyModeDto>>;
};

export interface LockParams {
  engineId: number
  lockName: string
}

export interface LockResponse {
  engineId: number
  lockName: string
  lockResult: EngineLockResults
}

export interface UnlockParams {
  engineId: number
}

export interface UnlockResponse {
  engineId: number
  unlockResult: EngineUnlockResults
}

export default provider