import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import { List, Datagrid, TextField, ListProps, Pagination, TextInput } from 'react-admin'
import { ResourceName } from '../../../core/ResourceName'

export const PalmList = (props: ListProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  return (
    <List
      {...props}
      perPage={20}
      pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
      exporter={false}
      filters={[
        <TextInput source="name" alwaysOn />,
        <TextInput source="categoryId" />
      ]}
    >
      <>
        <Datagrid
          size="small"
          rowClick={(id) => `${ResourceName.PALMS}/${id}/show`}
        >
          <TextField source="id" label="ID" />
          <TextField source="name" />
          <TextField source="categoryId" sortable={false} />
          {!smallScreen &&
            <TextField source="disabled" sortable={false} />}
          {!smallScreen &&
            <TextField source="mobileDeviceId" sortable={false} />}
        </Datagrid>
      </>
    </List>
  )
}
