import { Authority } from './Authority'

const mapping: Record<Authority, string[]> = {
  [Authority.VIEW_LOGS]: ['ViewItems_Logs'],
  [Authority.VIEW_STATS_OVERALL_VIEW]: ['ViewItems_Stats_OverallView'],
  [Authority.VIEW_EVENTS]: ['ViewItems_Events_Show', 'ViewItems_Events_Edit', 'ViewItems_Events_Add'],
  [Authority.EDIT_EVENTS]: ['ViewItems_Events_Edit'],
  [Authority.CREATE_EVENTS]: ['ViewItems_Events_Add'],
}

export const mapAuthorityToRequiredPermissions = (authority: Authority) => mapping[authority]