import { Box, Typography, Divider, useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  List,
  ListProps,
  useListContext,
  useTranslate,
  Datagrid,
  TextField,
  TextInput,
  FunctionField
} from 'react-admin'
import { CustomerInfo } from '../../core/customer-info/customer-info'
import { ResourceName } from '../../core/ResourceName'
import { MaskField } from '../common/MaskField'

const CustomerInfoGrid = () => {
  const {
    loading,
    filterValues: { info },
  } = useListContext()
  const t = useTranslate()
  const theme = useTheme()
  const smallDevice = useMediaQuery(theme.breakpoints.down('sm'))
  return (
    <>
      {info &&
        <Box m={2}>
          <Typography variant="h5" paragraph>
            {t('resources.customer-info.search-list-title', { info })}
          </Typography>
        </Box>}
      <Divider />
      <Datagrid
        loaded={!loading}
        rowClick={(id) => `${ResourceName.CUSTOMER_DETAILS}/${id}/show`}
      >
        <TextField source="firstname" />
        <TextField source="lastname" />
        {smallDevice &&
          <FunctionField<CustomerInfo> source="pesel"
            label="resources.customer-info.fields.documentNumber"
            render={(record?: CustomerInfo) => (
              record?.pesel ?
                <MaskField source="pesel" /> :
                <MaskField source="documentNumber" />
            )} />}
        {!smallDevice && <MaskField source="pesel" />}
        {!smallDevice && <MaskField source="documentNumber" />}
      </Datagrid>
    </>
  )
}

export const CustomerInfoList = (props: ListProps) => (
  <List
    exporter={false}
    {...props}
    sort={{ field: 'lastname', order: 'ASC' }}
    filters={[
      <TextInput source="firstname" />,
      <TextInput source="lastname" />,
      <TextInput source="pesel" />,
      <TextInput source="documentNumber" />,
      <TextInput source="info" />,
    ]}
  >
    <CustomerInfoGrid />
  </List>
)
