import { EventDto } from "../dto/event/event.dto"

const mapping: Partial<Record<keyof EventDto, string>> = {
  id: 'e=>e.Id',
  name: 'e=>e.Name',
  dateStart: 'e=>e.DateStart',
  dateEnd: 'e=>e.DateEnd',
  status: 'e=>e.Status'
}

export const mapSortEventParam = (key: keyof EventDto) => mapping[key]
