import {
  Create, CreateProps, TextInput, DateTimeInput, useTranslate, BooleanInput, required,
  ResourceContextProvider, TabbedForm, useListContext, TextField,
  useQuery, sanitizeFetchType, GET_LIST, List, Pagination, FormTab
} from "react-admin"
import React from "react"
import { useField } from "react-final-form"
import { ButtonGroup, TableContainer, Button, Table, TableHead, TableRow, TableCell, TableBody, Checkbox } from "@material-ui/core"
import { EventDto } from "../../core/dto/event/event.dto"
import { ResourceName } from "../../core/ResourceName"
import { ObjectDto } from "../../core/dto/object.dto"

const CreateTitle = () => {
  const translate = useTranslate()
  return (
    <span>{translate('resources.events.titles.create')}</span>
  )
}

const ObjectsInput = ({ source }: { source: keyof EventDto }) => {
  const translate = useTranslate()

  const {
    input: { onChange, value }
  } = useField(source)

  const { data, ids, filterValues, currentSort, total } = useListContext<ObjectDto>()

  const { data: allFiltered, refetch: refetchAllFiltered } = useQuery({
    type: sanitizeFetchType(GET_LIST),
    resource: ResourceName.OBJECTS,
    payload: {
      filter: {
        ...filterValues
      },
      sort: currentSort || {
        field: "id",
        order: "DESC"
      },
      pagination: {
        page: 1,
        perPage: total || 1
      }
    }
  })

  const handleAttach = (objectIds: number[]) => {
    onChange([...value, ...objectIds])
  }

  const handleDetach = (objectIds: number[]) => {
    onChange(value.filter((objectId: number) => !(objectIds.includes(objectId))))
  }

  const handleAttachAll = () => {
    refetchAllFiltered()
    handleAttach(allFiltered)
  }

  const handleDetachAll = () => {
    refetchAllFiltered()
    handleDetach(allFiltered)
  }

  const isAttached = (id: number) => (
    value.includes(id)
  )

  return (
    <TableContainer>
      <ButtonGroup>
        <Button variant="contained" onClick={() => handleAttachAll()}>
          {translate('resources.events.actions.attachAll')}</Button>
        <Button variant="contained" onClick={() => handleDetachAll()}>
          {translate('resources.events.actions.detachAll')}</Button>
      </ButtonGroup>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              {translate(`resources.events.fields.attachment.name`)}
            </TableCell>
            <TableCell style={{ width: "10%" }}>
              {translate(`resources.events.fields.attachment.attached`)}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <TableRow key={id}>
              <TableCell>
                <TextField source="name" record={data[id]} />
              </TableCell>
              <TableCell>
                <Checkbox
                  checked={isAttached(id as number)}
                  onClick={
                    isAttached(id as number)
                      ? () => handleDetach([id as number])
                      : () => handleAttach([id as number])
                  }
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export const EventCreate = (props: CreateProps) => {
  const { basePath } = props
  const translate = useTranslate()
  return (
    <Create {...props} title={<CreateTitle />}>
      <TabbedForm redirect="show">
        <FormTab label={translate("resources.events.tabs.general")}>
          <TextInput source="name" isRequired validate={required()} />
          <DateTimeInput source="dateStart" isRequired validate={required()} />
          <DateTimeInput source="dateEnd" isRequired validate={required()} />
          <TextInput source="externalId" />
          <TextInput source="infix" />
          <BooleanInput source="active" />
        </FormTab>
        <FormTab label={translate("resources.events.tabs.objects")}>
          <ResourceContextProvider value={ResourceName.OBJECTS}>
            <List basePath={basePath} title=" " actions={false}
              perPage={20} pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}>
              <ObjectsInput source="objectsIds" />
            </List>
          </ResourceContextProvider>
        </FormTab>
      </TabbedForm>
    </Create>
  )
}