import { Box, Button, ButtonGroup, Checkbox, Divider, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core'
import React, { useMemo } from "react"
import { DateField, EditButton, FunctionField, GET_LIST, List, Pagination, Record, ResourceContextProvider, sanitizeFetchType, Show, ShowProps, Tab, TabbedShowLayout, TextField, TextInput, useListContext, useMutation, useNotify, useQuery, useRefresh, useShowContext, useTranslate } from "react-admin"
import { CompanyDto } from '../../core/dto/company.dto'
import { EngineDto } from "../../core/dto/device/engine/engine.dto"
import { PalmDto } from "../../core/dto/device/palm/palm.dto"
import { EventCompaniesDto } from '../../core/dto/event/event-companies.dto'
import { EventDevicesDto } from '../../core/dto/event/event-devices.dto'
import { EventDto } from '../../core/dto/event/event.dto'
import { ObjectDto } from '../../core/dto/object.dto'
import { EventStatus } from "../../core/enum/EventStatus"
import { ResourceName } from "../../core/ResourceName"
import { useHasAuthority } from '../hooks/useHasAuthority'
import { Authority } from '../../core/auth/Authority'

const ShowTitle = (record: any) => {
  const { record: { name, dateStart, id } } = record
  const translate = useTranslate()
  const date = useMemo(() => !(dateStart instanceof Date) ? dateStart.replace('T', ' ') : '', [dateStart])
  return (
    <span>{translate('resources.events.titles.show')}: {name} {date} {id}</span>
  )
}

const Attachments = ({ source }: { source: 'COMPANIES' | 'OBJECTS' | 'ENGINES' | 'PALMS' }) => {
  const translate = useTranslate()
  const [mutate] = useMutation()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()

  const { data, ids, filterValues, currentSort, total, refetch: refetchList } = useListContext<CompanyDto | ObjectDto | EngineDto | PalmDto>()
  const { record: event, refetch } = useShowContext<EventDto>()
  const { data: allFiltered, refetch: refetchAllFiltered } = useQuery({
    type: sanitizeFetchType(GET_LIST),
    resource: source.toLocaleLowerCase(),
    payload: {
      filter: {
        ...filterValues
      },
      sort: currentSort || {
        field: "id",
        order: "DESC"
      },
      pagination: {
        page: 1,
        perPage: total || 1
      }
    }
  })

  const getAttachmentsFetchType = () => {
    switch (source) {
      case 'COMPANIES':
        return `getCompaniesList`
      case 'ENGINES':
      case 'PALMS':
        return `getDevicesList`
      default:
        return ''
    }
  }

  const { data: attachedDto, refetch: refetchAttachedIds } = useQuery({
    type: sanitizeFetchType(getAttachmentsFetchType()),
    resource: ResourceName.EVENTS,
    payload: {
      eventId: event?.id,
    }
  }, {
    enabled: getAttachmentsFetchType() !== ''
  })

  const isAttached = (attachmentId: number) => {
    switch (source) {
      case 'COMPANIES':
        return (attachedDto as EventCompaniesDto)?.companiesIds?.includes(attachmentId) === true
      case 'OBJECTS':
        return event?.objectsIds?.includes(attachmentId)
      case 'ENGINES':
      case 'PALMS':
        return (attachedDto as EventDevicesDto)?.verifierDevicesIds?.includes(attachmentId) === true
      default:
        notify(`Source '${source}' is not implemented yet.`)
        return false
    }
  }

  const handleAttach = async (idsToAttach: number[]) => {
    switch (source) {
      case 'COMPANIES':
        await mutate({
          type: sanitizeFetchType("attachCompanies"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            companiesIds: idsToAttach
          }
        }, { returnPromise: true })
        break;
      case 'OBJECTS':
        await mutate({
          type: sanitizeFetchType("attachObjects"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            objectsIds: idsToAttach
          }
        }, { returnPromise: true })
        break;
      case 'ENGINES':
      case 'PALMS':
        await mutate({
          type: sanitizeFetchType("attachDevices"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            verifierDevicesIds: idsToAttach
          }
        }, { returnPromise: true })
        break;
      default:
        notify(`Source '${source}' is not implemented yet.`);
    }
    refetchList()
    if (refetch) {
      refetch()
    }
    refetchAttachedIds()
  }

  const handleDetach = async (idsToDetach: number[]) => {
    switch (source) {
      case 'COMPANIES':
        await mutate({
          type: sanitizeFetchType("detachCompanies"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            companiesIds: idsToDetach
          }
        }, { returnPromise: true })
        break;
      case 'OBJECTS':
        await mutate({
          type: sanitizeFetchType("detachObjects"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event?.id,
            objectsIds: idsToDetach
          }
        }, { returnPromise: true })
        break;
      case 'ENGINES':
      case 'PALMS':
        await mutate({
          type: sanitizeFetchType("detachDevices"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: event && event.id,
            verifierDevicesIds: idsToDetach
          }
        }, { returnPromise: true })
        break;
      default:
        notify(`Source '${source}' is not implemented yet.`);
    }
    refetchList()
    if (refetch) {
      refetch()
    }
    refetchAttachedIds()
  }

  const handleAttachAll = () => {
    refetchAllFiltered()
    handleAttach(allFiltered.map((record: CompanyDto | EngineDto | PalmDto | ObjectDto) => record?.id))
  }

  const handleDetachAll = () => {
    refetchAllFiltered()
    handleDetach(allFiltered.map((record: CompanyDto | EngineDto | PalmDto | ObjectDto) => record?.id))
  }

  return (
    <TableContainer>
      <ButtonGroup>
        <Button variant="contained"
                disabled={!hasAuthority(Authority.EDIT_EVENTS)}
                onClick={() => handleAttachAll()}>
          {translate('resources.events.actions.attachAll')}</Button>
        <Button variant="contained"
                disabled={!hasAuthority(Authority.EDIT_EVENTS)}
                onClick={() => handleDetachAll()}>
          {translate('resources.events.actions.detachAll')}</Button>
      </ButtonGroup>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              {translate(`resources.events.fields.attachment.name`)}
            </TableCell>
            <TableCell style={{ width: "10%" }}>
              {translate(`resources.events.fields.attachment.attached`)}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <TableRow key={id}>
              <TableCell>
                <TextField source="name" record={data[id]} />
              </TableCell>
              <TableCell>
                <Checkbox
                  disabled={!hasAuthority(Authority.EDIT_EVENTS)}
                  checked={isAttached(id as number)}
                  onClick={
                    isAttached(id as number)
                      ? () => handleDetach([id as number])
                      : () => handleAttach([id as number])
                  }
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const GeneralEditButton = () => {
  const { record } = useShowContext()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()
  const hasAuthority = useHasAuthority()

  const handleActivation = async () => {
    if (record?.status !== EventStatus.IN_TRANSITION) {
      if (record?.status === EventStatus.ACTIVE) {
        await mutate({
          type: sanitizeFetchType("deactivate"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: record?.id
          }
        }, { returnPromise: false })
      }
      else {
        await mutate({
          type: sanitizeFetchType("activate"),
          resource: ResourceName.EVENTS,
          payload: {
            eventId: record?.id
          }
        }, { returnPromise: false })
      }
      refresh()
    }
  }

  return (
    <>
      <Divider />
      <Box p={1} display="flex" justifyContent="flex-end">
        <Button variant="contained"
                onClick={handleActivation}
                color="primary"
                style={{ marginRight: '5px' }}
                disabled={!hasAuthority(Authority.EDIT_EVENTS) || record?.status === EventStatus.IN_TRANSITION}>
          {record?.status === EventStatus.INACTIVE ?
            translate('resources.events.actions.activate') :
            translate('resources.events.actions.deactivate')}
        </Button>
        <EditButton variant="contained"
                    basePath="/events"
                    record={record}
                    disabled={!hasAuthority(Authority.EDIT_EVENTS)} />
      </Box>
    </>
  )
}

export const EventShow = (props: ShowProps) => {
  const translate = useTranslate()
  const { basePath } = props
  return (
    <Show {...props} title={<ShowTitle />} actions={false}>
      <TabbedShowLayout>
        <Tab label={translate("resources.events.tabs.general")}>
          <TextField source="id" label="ID" />
          <TextField source="name" />
          <DateField source="dateStart" showTime />
          <DateField source="dateEnd" showTime />
          <FunctionField resource="events" label={translate("resources.events.fields.status")}
            render={(r: Record | undefined) =>
              (translate(`resources.enums.eventStatus.${r?.status.toLowerCase()}`))} />
          <GeneralEditButton />
        </Tab>
        <Tab label={translate("resources.events.tabs.palms")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.PALMS} >
                <List basePath={basePath} exporter={false} title=" "
                  perPage={20} pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
                  filters={[
                    <TextInput source="name" alwaysOn />,
                    <TextInput source="categoryId" />
                  ]}>
                  <Attachments source="PALMS" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
        <Tab label={translate("resources.events.tabs.engines")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.ENGINES}>
                <List basePath={basePath} exporter={false} title=" "
                  perPage={20} pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
                  filters={[
                    <TextInput source="name" alwaysOn />,
                    <TextInput source="categoryId" />
                  ]}>
                  <Attachments source="ENGINES" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
        <Tab label={translate("resources.events.tabs.objects")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.OBJECTS}>
                <List basePath={basePath} exporter={false} title=" "
                  perPage={20} pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
                  filters={[
                    <TextInput source="name" alwaysOn />
                  ]}>
                  <Attachments source="OBJECTS" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
        <Tab label={translate("resources.events.tabs.companies")}>
          <>
            <Box paddingY={2}>
              <ResourceContextProvider value={ResourceName.COMPANIES}>
                <List basePath={basePath} exporter={false} title=" "
                  perPage={20} pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
                  filters={[
                    <TextInput source="name" alwaysOn />
                  ]}>
                  <Attachments source="COMPANIES" />
                </List>
              </ResourceContextProvider>
            </Box>
          </>
        </Tab>
      </TabbedShowLayout>
    </Show>
  )
}