import React, { useState } from 'react'
import {
  useTranslate,
  useShowContext,
  useMutation,
  useRefresh,
  sanitizeFetchType,
} from 'react-admin'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  TextField,
  ButtonGroup,
} from '@material-ui/core'
import { ResourceName } from '../../../core/ResourceName'
import { PalmDto } from '../../../core/dto/device/palm/palm.dto'

export const ConfirmDialog = ({
  confirm,
  cancel,
  source,
  open,
  action,
}: {
  confirm: () => void
  cancel: () => void
  source: 'deviceId' | 'lock'
  open: boolean
  action: 'set' | 'remove'
}) => {
  const translate = useTranslate()
  return (
    <Dialog open={open}>
      <DialogTitle>
        {translate(`resources.palms.dialogs.confirm.${source}.${action}`)}
      </DialogTitle>
      <DialogActions>
        <Button variant="contained" onClick={confirm}>
          {translate('ra.action.save')}
        </Button>
        <Button variant="contained" onClick={cancel}>
          {translate('const.cancel')}
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export const DeviceIdModal = ({
  open,
  close,
}: {
  open: boolean
  close: () => void
}) => {
  const [value, setValue] = useState('')
  const [error, setError] = useState(false)
  const [errorText, setErrorText] = useState('')
  const [confirmOpen, setConfirmOpen] = useState(false)
  const { record: palm } = useShowContext<PalmDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleClose = () => {
    setValue('')
    setError(false)
    setErrorText('')
    close()
  }

  const handleSetMobileDeviceId = async () => {
    setConfirmOpen(false)
    if (value.replace(/\s/g, '').length === 0) {
      setError(true)
      setErrorText(
        translate('resources.palms.dialogs.deviceId.error-bad-string'),
      )
    } else if (value.replace(/\s/g, '').length !== value.length) {
      setError(true)
      setErrorText(
        translate('resources.palms.dialogs.deviceId.error-no-spaces'),
      )
    } else {
      await mutate(
        {
          type: sanitizeFetchType('setMobileDeviceId'),
          resource: ResourceName.PALMS,
          payload: {
            palmId: palm?.id,
            mobileDeviceId: value,
          },
        },
        { returnPromise: true },
      )
      setValue('')
      setError(false)
      setErrorText('')
      refresh()
      close()
    }
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <ConfirmDialog
        open={confirmOpen}
        cancel={() => setConfirmOpen(false)}
        confirm={handleSetMobileDeviceId}
        source="deviceId"
        action="set"
      />
      <DialogTitle>
        {translate('resources.palms.dialogs.deviceId.title')}
      </DialogTitle>
      <DialogContent>
        <TextField
          variant="outlined"
          value={value}
          onChange={(e) => setValue(e.target.value)}
          fullWidth
          error={error}
          helperText={errorText}
        />
      </DialogContent>
      <DialogActions>
        <ButtonGroup>
          <Button variant="contained" onClick={() => setConfirmOpen(true)}>
            {translate('ra.action.save')}
          </Button>
          <Button variant="contained" onClick={handleClose}>
            {translate('const.cancel')}
          </Button>
        </ButtonGroup>
      </DialogActions>
    </Dialog>
  )
}

export const LockModal = ({
  open,
  close,
}: {
  open: boolean
  close: () => void
}) => {
  const [value, setValue] = useState('')
  const [error, setError] = useState(false)
  const [errorText, setErrorText] = useState('')
  const [confirmOpen, setConfirmOpen] = useState(false)
  const { record: palm } = useShowContext<PalmDto>()
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleClose = () => {
    setValue('')
    setError(false)
    setErrorText('')
    close()
  }

  const handleLock = async () => {
    setConfirmOpen(false)
    if (value.replace(/\s/g, '').length === 0) {
      setError(true)
      setErrorText(translate('resources.palms.dialogs.lock.error-bad-string'))
    } else if (value.replace(/\s/g, '').length !== value.length) {
      setError(true)
      setErrorText(translate('resources.palms.dialogs.lock.error-no-spaces'))
    } else {
      await mutate(
        {
          type: sanitizeFetchType('lock'),
          resource: ResourceName.PALMS,
          payload: {
            palmId: palm?.id,
            lockName: value,
          },
        },
        { returnPromise: true },
      )
      handleClose()
      refresh()
    }
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <ConfirmDialog
        open={confirmOpen}
        cancel={() => setConfirmOpen(false)}
        confirm={handleLock}
        source="lock"
        action="set"
      />
      <DialogTitle>
        {translate('resources.palms.dialogs.lock.title')}
      </DialogTitle>
      <DialogContent>
        <TextField
          variant="outlined"
          value={value}
          onChange={(e) => setValue(e.target.value)}
          fullWidth
          error={error}
          helperText={errorText}
        />
      </DialogContent>
      <DialogActions>
        <ButtonGroup>
          <Button variant="contained" onClick={() => setConfirmOpen(true)}>
            {translate('ra.action.save')}
          </Button>
          <Button variant="contained" onClick={handleClose}>
            {translate('const.cancel')}
          </Button>
        </ButtonGroup>
      </DialogActions>
    </Dialog>
  )
}
