import { GetListResult, DataProvider } from "react-admin";
import { CUSTOMERS_URL } from "../api-urls";
import { getWithPagination } from "../common/fetch.utils";
import { CustomerDto } from "../dto/customer.dto";
import { CustomerInfo } from "./customer-info";
import { getFilterMapper } from "./customer-info-filter.mapper";
import { mapSortCustomerParams } from "./customer-info-sort.mapper";
import { CustomerInfoFilter } from "./customer-info.params";

const getFilterParams = (
  partialValues: Partial<CustomerInfo> & { readonly info: string },
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof CustomerInfo)
      return mapper('c', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}

const provider = {
  getList: async (
    resource: string,
    { filter, sort, pagination }: CustomerInfoFilter
  ): Promise<GetListResult<CustomerInfo>> => {
    const filterParams = `c=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    if (!!pagination && !!sort) {
      queryParams.push(`sortBy=${mapSortCustomerParams(sort.field)}`)
      queryParams.push(`sortOrder=${sort.order}`)
      queryParams.push(`rangeFrom=${pagination.page * pagination.perPage - pagination.perPage}`)
      queryParams.push(`rangeTo=${pagination.page * pagination.perPage}`)
    }
    else queryParams.push('')
    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`

    const { data, range: { total } } = await getWithPagination<CustomerDto[]>(
      `${CUSTOMERS_URL}/GetByConditions`,
      path
    )

    return Promise.resolve({
      data,
      total
    })
  }
} as DataProvider

export default provider