import {
  useMediaQuery,
  useTheme,
  Grid,
  Divider,
  TableBody,
  Box,
  Typography,
} from '@material-ui/core'
import { Receipt } from '@material-ui/icons'
import React from 'react'
import {
  List,
  ListProps,
  Pagination,
  SelectInput,
  TextInput,
  useTranslate,
  Datagrid,
  TextField,
  DateField,
  Record,
  FunctionField,
  useLocale,
  DatagridBodyProps,
  DatagridRow,
  DatagridHeader,
  DatagridHeaderProps,
  ShowButton
} from 'react-admin'
import { useHistory } from 'react-router'
import { getContrastColor } from '../../core/common/visual.utils'
import { ExtendedLog } from '../../core/logs/extended-log'
import { DateFilterInput } from './DateFilterInput'
import { VCenterGrid, VHCenterGrid } from './LogsComponents'

interface LogExpandProps {
  readonly noBooking?: boolean
  readonly record?: ExtendedLog
}

interface LogBodyProps extends DatagridBodyProps {
  readonly noBooking?: boolean
  readonly compactMode?: boolean
}

interface LogHeaderProps extends DatagridHeaderProps {
  readonly noBooking?: boolean
  readonly compactMode?: boolean
}

interface LogListProps extends ListProps {
  readonly compactMode?: boolean
  readonly noBooking?: boolean
}

const BookingButton = ({ style }: { style?: any }) => {
  const theme = useTheme()
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const history = useHistory()
  return (
    <FunctionField
      source="bookingId"
      render={(record: Record | undefined) =>
        record?.bookingId && (
          <>
            <ShowButton
              basePath="/ticket-details"
              label={record.bookingId.toString()}
              record={{ id: record.bookingId }}
              icon={<Receipt />}
              style={style}
            />
            {xSmallScreen &&
              <Typography color="primary" onClick={() => history.push(`/ticket-details/${record.bookingId}/show`)}>{record.bookingId}</Typography>}
          </>
        )
      }
    />
  )
}

const LogExpand = ({ noBooking, record }: LogExpandProps) => {
  const translate = useTranslate()
  return (
    <Box padding={1}>
      <Grid container spacing={1}>
        <VCenterGrid item xs={4}>
          {translate('resources.logs.fields.description')}:
        </VCenterGrid>
        <VCenterGrid item xs={8}>
          <TextField source="text" />
        </VCenterGrid>
        {!noBooking && record?.bookingId && (
          <>
            <Grid item xs={12}>
              <Divider />
            </Grid>
            <VCenterGrid item xs={4}>
              {translate('resources.logs.fields.bookingId')}:
            </VCenterGrid>
            <VHCenterGrid item xs={8}>
              <BookingButton />
            </VHCenterGrid>
          </>
        )}
      </Grid>
    </Box>
  )
}

const LogBody = ({
  noBooking,
  compactMode,
  data,
  ids,
  rowStyle,
  expand
}: LogBodyProps) => (
  <TableBody>
    {data && rowStyle &&
      ids?.map((id) => {
        const style = rowStyle(data[id], id as number)
        return (
          <DatagridRow
            key={id}
            id={id}
            record={data[id]}
            resource="logs"
            expand={expand}
            style={style}
          >
            <DateField
              source="date"
              style={style}
            />
            <TextField
              source="deviceName"
              style={style}
            />
            <TextField
              source="ticketCode"
              style={style}
            />
            {!compactMode && (
              <TextField
                source="text"
                style={style}
              />
            )}
            {!compactMode && !noBooking && (
              <BookingButton style={style} />
            )}
          </DatagridRow>
        )
      })}
  </TableBody>
)

const LogHeader = ({ noBooking, compactMode, ...props }: LogHeaderProps) => (
  <DatagridHeader {...props}>
    <DateField source="date"
      label="resources.logs.fields.date" />
    <TextField source="deviceName"
      label="resources.logs.fields.deviceName" />
    <TextField source="ticketCode"
      label="resources.logs.fields.ticketCode" />
    {!compactMode && (
      <TextField
        source="text"
        sortable={false}
        label="resources.logs.fields.description"
      />
    )}
    {!noBooking && !compactMode && <TextField source="bookingId"
      label="resources.logs.fields.bookingId" />}
  </DatagridHeader>
)

export const LogList = ({
  compactMode = false,
  noBooking = false,
  ...props
}: LogListProps) => {
  const { filter, ...listProps } = props
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  const locale = useLocale()
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={<Pagination rowsPerPageOptions={[20, 50, 100]} />}
      filters={[
        <TextInput source="ticketCode" alwaysOn />,
        <SelectInput
          source="deviceName"
          choices={[
            {
              id: 'Czytnik',
              name: 'resources.logs.fields.deviceNameChoices.reader',
            },
            {
              id: 'Przej\u015Bcie',
              name: 'resources.logs.fields.deviceNameChoices.gateway',
            },
          ]}
        />,
        <DateFilterInput source="date" key="date" />,
        <TextInput source="bookingId" />,
        <TextInput source="event" />,
      ]}
      filter={{ ...filter, locale }}
      sort={{ field: 'date', order: 'DESC' }}
      {...listProps}
    >
      <>
        <Datagrid
          header={
            <LogHeader
              compactMode={smallScreen ? true : compactMode}
              noBooking={noBooking}
            />
          }
          body={
            <LogBody
              compactMode={smallScreen ? true : compactMode}
              noBooking={noBooking}
            />
          }
          isRowExpandable={() => smallScreen || compactMode}
          expand={<LogExpand noBooking={noBooking} />}
          rowStyle={(record: Record) => ({
            background: record.color,
            color: record.color !== 'transparent' ? getContrastColor(record.color) : 'inherit'
          })}
          padding={xSmallScreen ? "none" : "default"}
        >
          <div />
        </Datagrid>
      </>
    </List>
  )
}