import {
  Datagrid,
  DateField,
  Filter,
  FilterProps,
  FunctionField,
  Link,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
  useTranslate,
} from 'react-admin'
import { useListContext } from 'ra-core'
import React from 'react'
import { IconButton, makeStyles, useMediaQuery, useTheme } from '@material-ui/core'
import { Event as EventIcon, Sort } from '@material-ui/icons'
import { ResourceName } from '../../core/ResourceName'
import { EventDto } from '../../core/dto/event/event.dto'
import { useHasAuthority } from '../hooks/useHasAuthority'
import { Authority } from '../../core/auth/Authority'
import { DateTime } from '../../core/common/date-time'

const useStyles = makeStyles((theme) => ({
  actionColumnsWithoutPadding: {
    // applies always for last column
    '& tr > td:last-child': {
      padding: 0,
      width: theme.spacing(6),
    },
    // only applies when there are four columns and targets the penultimate column
    '& tr > td:first-child:nth-last-child(4) ~ td:nth-last-child(2)': {
      padding: 0,
      width: theme.spacing(6),
    },
  },
}))

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput source="name" alwaysOn />
  </Filter>
)

const LastEvents = () => {
  const t = useTranslate()
  const classes = useStyles()
  const hasAuthority = useHasAuthority()
  return (
    <Datagrid className={classes.actionColumnsWithoutPadding}>
      <FunctionField<EventDto>
        label={t('resources.events.fields.name')}
        render={(record?: EventDto) => {
          const { id, name } = record as EventDto
          return `${id}. ${name}`
        }}
      />
      <FunctionField<EventDto>
        label={t('resources.events.fields.status')}
        render={(record?: EventDto) => {
          const { status } = record as EventDto
          return t(`resources.enums.eventStatus.${status.toLowerCase()}`)
        }}
      />
      <FunctionField<EventDto>
        render={(record?: EventDto) => {
          const { id } = record as EventDto
          return (
            <Link to={`/${ResourceName.EVENTS}/${id}/show`}>
              <IconButton>
                <EventIcon />
              </IconButton>
            </Link>
          )
        }}
      />
      {hasAuthority(Authority.VIEW_STATS_OVERALL_VIEW) && (
        <FunctionField<EventDto>
          render={(record?: EventDto) => {
            const { id } = record as EventDto
            return (
              <Link to={`/${ResourceName.STATS_OVERALL_VIEW}/${id}/show`}>
                <IconButton>
                  <Sort style={{ transform: 'scaleX(-1)' }} />
                </IconButton>
              </Link>
            )
          }}
        />
      )}
    </Datagrid>
  )
}

const EventDateField = ({ source, label }: { source: string, label?: string }) => (
  <FunctionField<EventDto> source={source} label={label} render={(record?: EventDto) => {
    const { dateStart, dateEnd } = {
      dateStart: new Date(record?.dateStart as DateTime),
      dateEnd: new Date(record?.dateEnd as DateTime)
    }
    if (dateStart.toDateString() === dateEnd.toDateString()) return (
      <>
        <DateField source="dateStart" />
        <span> {dateStart.getHours() < 10 && 0}{dateStart.getHours()}:</span>
        <span>{dateStart.getMinutes() < 10 && 0}{dateStart.getMinutes()} - </span>
        <span>{dateEnd.getHours() < 10 && 0}{dateEnd.getHours()}:</span>
        <span>{dateEnd.getMinutes() < 10 && 0}{dateEnd.getMinutes()}</span>
      </>
    )
    return (
      <>
        <span>
          <DateField source="dateStart" showTime />
        </span>
        <span> - </span>
        <span>
          <DateField source="dateEnd" showTime />
        </span>
      </>
    )
  }} />
)

const EventGrid = ({ compactMode }: { compactMode?: boolean }) => {
  const { loading } = useListContext()
  const t = useTranslate()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  return (
    <>
      {compactMode ? (
        <LastEvents />
      ) : (
        <Datagrid
          size="small"
          loaded={!loading}
          rowClick={(id) => `${ResourceName.EVENTS}/${id}/show`}
        >
          <TextField source="id" label="ID" />
          <TextField source="name" />
          {!smallScreen &&
            <DateField source="dateStart" showTime />}
          {!smallScreen &&
            <DateField source="dateEnd" showTime />}
          {smallScreen &&
            <EventDateField source="dateStart"
              label="resources.events.fields.date" />}
          <FunctionField<EventDto>
            resource="events"
            label={t('resources.events.fields.status')}
            render={(r?: EventDto) =>
              t(`resources.enums.eventStatus.${r?.status.toLowerCase()}`)
            }
          />
        </Datagrid>
      )}
    </>
  )
}

interface EventListProps extends ListProps {
  compactMode?: boolean
}

export const EventList = ({ compactMode = false, ...props }: EventListProps) =>
  compactMode ? (
    <List
      {...props}
      actions={false}
      filters={undefined}
      perPage={10}
      pagination={false}
    >
      <EventGrid compactMode />
    </List>
  ) : (
    <List
      {...props}
      exporter={false}
      filters={<ListFilters />}
      perPage={20}
      pagination={<Pagination rowsPerPageOptions={[10, 20, 50]} />}
    >
      <EventGrid />
    </List>
  )
