import { Typography } from '@material-ui/core'
import { Cancel } from '@material-ui/icons'
import React from 'react'
import {
  BooleanInput,
  DateTimeInput, Edit,
  EditProps, FunctionField, Labeled, Record, SaveButton, SelectInput, ShowButton, SimpleForm,
  TextField,
  TextInput, TitleProps,
  Toolbar,
  ToolbarProps, useEditContext, useTranslate
} from 'react-admin'
import { EngineDto } from '../../../core/dto/device/engine/engine.dto'
import { DeviceDirection } from '../../../core/enum/DeviceDirection'
import { Multiplicities } from '../../../core/enum/Multiplicities'

interface EngineTitleProps extends TitleProps {
  record?: EngineDto
}

const EditTitle = (data: EngineTitleProps) => {
  const {
    record,
  } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.engines.titles.edit')}: {record?.id}. {record?.name}{' '}
    </span>
  )
}

const HidingField = ({ source }: { source: keyof EngineDto }) => {
  const { record } = useEditContext<EngineDto>()
  return (
    <>
      {record && record[source] && (
        <Labeled label="resources.engines.fields.disabled">
          <Typography component="span" color="textPrimary">
            {record[source]}
          </Typography>
        </Labeled>
      )}
    </>
  )
}

const EditToolbar = (props: ToolbarProps) => (
  <Toolbar {...props}>
    <SaveButton />
    <ShowButton icon={<Cancel />} label="const.cancel" />
  </Toolbar>
)

export const EngineEdit = (props: EditProps) => {
  const translate = useTranslate()
  return (
    <Edit {...props} hasShow={false} title={<EditTitle />}>
      <SimpleForm redirect="show" toolbar={<EditToolbar />}>
        <TextField source="id" label="ID" />
        <TextInput source="name" />
        <BooleanInput source="isWatched" />
        <DateTimeInput source="lastRefresh" />
        <TextInput source="address" />
        <SelectInput
          source="direction"
          choices={[
            { id: DeviceDirection.IN, name: translate(`resources.enums.deviceDirection.IN`) },
            { id: DeviceDirection.OUT, name: translate(`resources.enums.deviceDirection.OUT`) },
            { id: DeviceDirection.INFO, name: translate(`resources.enums.deviceDirection.INFO`) }
          ]}
        />
        <TextInput source="categoryId" />
        <FunctionField source="stanbyMode" render={(record: Record | undefined) => translate(`resources.enums.standbyMode.${record?.stanbyMode}`)} />
        <HidingField source="disabled" />
        <BooleanInput source="onlineInfixCeck" />
        <SelectInput
          source="multiplicity"
          choices={[
            { id: Multiplicities.SINGLE, name: translate(`resources.enums.multiplicities.SINGLE`) },
            { id: Multiplicities.MULTIPLE, name: translate(`resources.enums.multiplicities.MULTIPLE`) },
            { id: Multiplicities.CHECK_ALL, name: translate(`resources.enums.multiplicities.CHECK_ALL`) }
          ]}
        />
      </SimpleForm>
    </Edit>
  )
}



