import {
  Box,
  Button, ButtonGroup, Checkbox, Divider, Table, TableBody, TableCell, TableContainer, TableHead,
  TableRow
} from '@material-ui/core'
import React, { useState } from 'react'
import {
  DateField, EditButton, FunctionField, GET_LIST, List, Pagination, Record, ResourceContextProvider, sanitizeFetchType, Show,
  ShowProps, Tab, TabbedShowLayout, TextField, TextInput,
  TitleProps, useListContext, useMutation, useNotify, useQuery, useRefresh, useShowContext, useTranslate
} from 'react-admin'
import { PalmDto } from '../../../core/dto/device/palm/palm.dto'
import { EntranceDto } from '../../../core/dto/entrance.dto'
import { EventDto } from '../../../core/dto/event/event.dto'
import { ResourceName } from '../../../core/ResourceName'
import { ConfirmDialog, DeviceIdModal, LockModal } from './PalmDialogs'
import { useHasAuthority } from '../../hooks/useHasAuthority'
import { Authority } from '../../../core/auth/Authority'

interface PalmTitleProps extends TitleProps {
  record?: PalmDto
}

const ShowTitle = (data: PalmTitleProps) => {
  const {
    record: palm,
  } = data
  const translate = useTranslate()
  return (
    <span>
      {translate('resources.palms.titles.show')}: {palm?.id}. {palm?.name}
    </span>
  )
}

const GeneralActions = () => {
  const { record: palm } = useShowContext<PalmDto>()
  const [deviceIdModalOpen, setDeviceIdModalOpen] = useState(false)
  const [lockModalOpen, setLockModalOpen] = useState(false)
  const [unlockDialogOpen, setUnlockDialogOpen] = useState(false)
  const [resetIdDialogOpen, setResetIdDialogOpen] = useState(false)
  const [mutate] = useMutation()
  const refresh = useRefresh()
  const translate = useTranslate()

  const handleSetMobileDeviceId = async () => {
    setResetIdDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('resetMobileDeviceId'),
        resource: ResourceName.PALMS,
        payload: {
          palmId: palm?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleSetMobileDeviceIdDialog = () => {
    if (palm?.mobileDeviceId) setResetIdDialogOpen(true)
    else setDeviceIdModalOpen(true)
  }

  const handleLock = async () => {
    setUnlockDialogOpen(false)
    await mutate(
      {
        type: sanitizeFetchType('unlock'),
        resource: ResourceName.PALMS,
        payload: {
          palmId: palm?.id,
        },
      },
      { returnPromise: true },
    )
    refresh()
  }

  const handleLockDialog = () => {
    if (palm?.disabled) setUnlockDialogOpen(true)
    else setLockModalOpen(true)
  }

  return (
    <>
      <Divider />
      <DeviceIdModal
        open={deviceIdModalOpen}
        close={() => setDeviceIdModalOpen(false)}
      />
      <LockModal open={lockModalOpen} close={() => setLockModalOpen(false)} />
      <ConfirmDialog
        open={unlockDialogOpen}
        confirm={handleLock}
        source="lock"
        cancel={() => setUnlockDialogOpen(false)}
        action="remove"
      />
      <ConfirmDialog
        open={resetIdDialogOpen}
        confirm={handleSetMobileDeviceId}
        source="deviceId"
        cancel={() => setResetIdDialogOpen(false)}
        action="remove"
      />
      <Box p={1} display="flex" justifyContent="flex-end">
        <Button
          variant="contained"
          onClick={handleSetMobileDeviceIdDialog}
          color="primary"
          style={{ marginRight: '5px' }}
        >
          {palm?.mobileDeviceId
            ? translate('resources.palms.actions.resetMobileDeviceId')
            : translate('resources.palms.actions.setMobileDeviceId')}
        </Button>
        <Button
          variant="contained"
          onClick={handleLockDialog}
          color="primary"
          style={{ marginRight: '5px' }}
        >
          {palm?.disabled
            ? translate('resources.palms.actions.unlock')
            : translate('resources.palms.actions.lock')}
        </Button>
        <EditButton basePath="/palms" record={palm} variant="contained" />
      </Box>
    </>
  )
}

const Attachments = ({ source }: { source: 'EVENTS' | 'ENTRANCES' }) => {
  const { data: attachedList, ids, filterValues, currentSort, total, refetch: refetchList } = useListContext<EventDto | EntranceDto>()
  const { record: palm, refetch } = useShowContext<PalmDto>()
  const { data: allFiltered, refetch: refetchAllFiltered } = useQuery({
    type: sanitizeFetchType(GET_LIST),
    resource: source.toLocaleLowerCase(),
    payload: {
      filter: {
        ...filterValues
      },
      sort: currentSort || {
        field: "id",
        order: "DESC"
      },
      pagination: {
        page: 1,
        perPage: total || 1
      }
    }
  })
  const translate = useTranslate()
  const [mutate] = useMutation()
  const notify = useNotify()
  const hasAuthority = useHasAuthority()

  const isAttached = (attachmentId: number) => {
    switch (source) {
      case 'EVENTS':
        return palm?.eventsIds?.includes(attachmentId)
      case 'ENTRANCES':
        return palm?.entrancesIds?.includes(attachmentId)
      default:
        notify(`Source '${source}' is not implemented yet.`)
        return false
    }
  }

  const handleAttach = async (id: number[]) => {
    switch (source) {
      case 'EVENTS':
        await mutate(
          {
            type: sanitizeFetchType('attachEvents'),
            resource: ResourceName.PALMS,
            payload: {
              palmId: palm?.id,
              eventsIds: id,
            },
          },
          { returnPromise: true },
        )
        break
      case 'ENTRANCES':
        await mutate(
          {
            type: sanitizeFetchType('attachEntrances'),
            resource: ResourceName.PALMS,
            payload: {
              palmId: palm?.id,
              entrancesIds: id,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    refetchList()
    if (refetch){
      refetch()
    }
  }

  const handleDetach = async (id: number[]) => {
    switch (source) {
      case 'EVENTS':
        await mutate(
          {
            type: sanitizeFetchType('detachEvents'),
            resource: ResourceName.PALMS,
            payload: {
              palmId: palm?.id,
              eventsIds: id,
            },
          },
          { returnPromise: true },
        )
        break
      case 'ENTRANCES':
        await mutate(
          {
            type: sanitizeFetchType('detachEntrances'),
            resource: ResourceName.PALMS,
            payload: {
              palmId: palm?.id,
              entrancesIds: id,
            },
          },
          { returnPromise: true },
        )
        break
      default:
        notify(`Source '${source}' is not implemented yet.`)
    }
    refetchList()
    if (refetch){
      refetch()
    }
  }

  const handleAttachAll = () => {
    refetchAllFiltered()
    handleAttach(allFiltered.map((record: EventDto | EntranceDto) => record?.id))
  }

  const handleDetachAll = () => {
    refetchAllFiltered()
    handleDetach(allFiltered.map((record: EventDto | EntranceDto) => record?.id))
  }

  return (
    <TableContainer>
      <ButtonGroup>
        <Button
          variant="contained"
          disabled={source === 'EVENTS' && !hasAuthority(Authority.EDIT_EVENTS)}
          onClick={() => handleAttachAll()}
        >
          {translate('resources.palms.actions.attachAll')}
        </Button>
        <Button
          variant="contained"
          disabled={source === 'EVENTS' && !hasAuthority(Authority.EDIT_EVENTS)}
          onClick={() => handleDetachAll()}
        >
          {translate('resources.palms.actions.detachAll')}
        </Button>
      </ButtonGroup>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              {source === 'ENTRANCES'
                ? translate('resources.palms.fields.attachment.note')
                : translate(`resources.palms.fields.attachment.name`)}
            </TableCell>
            <TableCell style={{ width: '10%' }}>
              {translate(`resources.palms.fields.attachment.attached`)}
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map((id) => (
            <TableRow key={id}>
              <TableCell>
                {source === 'ENTRANCES' && (
                  <TextField source="note" record={attachedList[id]} />
                )}
                {source === 'EVENTS' && (
                  <TextField source="name" record={attachedList[id]} />
                )}
              </TableCell>
              <TableCell>
                <Checkbox
                  disabled={source === 'EVENTS' && !hasAuthority(Authority.EDIT_EVENTS)}
                  checked={isAttached(id as number)}
                  onClick={
                    isAttached(id as number)
                      ? () => handleDetach([id as number])
                      : () => handleAttach([id as number])
                  }
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const PalmTabs = () => {
  const translate = useTranslate()
  const { record: palm, basePath } = useShowContext<PalmDto>()
  const hasAuthority = useHasAuthority()

  return (
    <TabbedShowLayout>
      <Tab label={translate('resources.palms.tabs.general')}>
        <TextField label="ID" source="id" />
        <TextField label="resources.palms.fields.name" source="name" />
        <FunctionField
          label="resources.palms.fields.isWatched"
          source="isWatched"
          render={(record: Record | undefined) =>
            record?.isWatched ? translate('const.yes') : translate('const.no')
          }
        />
        <DateField
          label="resources.palms.fields.lastRefresh"
          source="lastRefresh"
          showTime
          options={{ day: "2-digit", month: "2-digit", year: "numeric", hour: "2-digit", minute: "2-digit" }}
        />
        <FunctionField
          label="resources.palms.fields.direction"
          source="direction"
          render={(record: Record | undefined) => translate(`resources.enums.deviceDirection.${record?.direction}`)}
        />
        <TextField
          label="resources.palms.fields.categoryId"
          source="categoryId"
        />
        {palm?.disabled && (
          <TextField
            label="resources.palms.fields.disabled"
            source="disabled"
          />
        )}
        <FunctionField
          label="resources.palms.fields.onlineInfixCeck"
          source="onlineInfixCeck"
          render={(record: Record | undefined) =>
            record?.onlineInfixCeck
              ? translate('const.yes')
              : translate('const.no')
          }
        />
        <FunctionField
          label="resources.palms.fields.multiplicity"
          source="multiplicity"
          render={(record: Record | undefined) => translate(`resources.enums.multiplicities.${record?.multiplicity}`)}
        />
        {palm?.mobileDeviceId && (
          <TextField
            label="resources.palms.fields.mobileDeviceId"
            source="mobileDeviceId"
          />
        )}
        <GeneralActions />
      </Tab>
      {hasAuthority(Authority.VIEW_EVENTS) && (
        <Tab label={translate('resources.palms.tabs.events')}>
          <ResourceContextProvider value={ResourceName.EVENTS}>
            <List
              basePath={basePath}
              filter={{ palmId: palm?.id }}
              exporter={false}
              title=" "
              filters={[<TextInput source="name" alwaysOn />]}
              sort={{ field: 'dateStart', order: 'DESC' }}
              pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
              perPage={20}
            >
              <Attachments source="EVENTS" />
            </List>
          </ResourceContextProvider>
        </Tab>
      )}
      <Tab label={translate('resources.palms.tabs.entrances')}>
        <ResourceContextProvider value={ResourceName.ENTRANCES}>
          <List
            basePath={basePath}
            filter={{ palmId: palm?.id }}
            exporter={false}
            title=" "
            filters={[<TextInput source="note" alwaysOn />]}
            pagination={<Pagination rowsPerPageOptions={[10, 20, 50, 100]} />}
            perPage={20}
          >
            <Attachments source="ENTRANCES" />
          </List>
        </ResourceContextProvider>
      </Tab>
    </TabbedShowLayout>
  )
}

export const PalmShow = (props: ShowProps) => (
  <Show {...props} actions={false} title={<ShowTitle />}>
    <PalmTabs />
  </Show>
)
