import {
  List,
  ListProps,
  useListContext,
  useTranslate,
  useMutation,
  sanitizeFetchType,
  UPDATE_MANY,
  TextField,
  DateField,
  FunctionField,
  DatagridHeaderProps,
  DatagridHeader,
  DatagridRow,
  DatagridBodyProps,
  Datagrid
} from 'react-admin'
import React from 'react'
import {
  Button,
  IconButton,
  TableBody,
  useMediaQuery,
  useTheme,
} from '@material-ui/core'
import { Power, PowerOff } from '@material-ui/icons'
import { AccessUsingDto } from '../../../core/dto/access-using.dto'
import { ResourceName } from '../../../core/ResourceName'
import { AccessUsing } from '../../../core/access-usings/access-using'

const AccessUsingsBody = ({ data, ids, expand }: DatagridBodyProps) => {
  const translate = useTranslate()
  const [mutate] = useMutation()
  const { refetch } = useListContext()
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))

  const patchAccess = async (access: AccessUsingDto) => {
    await mutate(
      {
        type: sanitizeFetchType(UPDATE_MANY),
        resource: ResourceName.ACCESS_USINGS,
        payload: {
          ids: [access.id],
          data: {
            active: !access.active,
          },
        },
      },
      { returnPromise: true },
    )
    refetch()
  }

  return (
    <TableBody>
      {ids && ids.map((id) =>
        data && data[id] && (
          <DatagridRow
            expand={expand}
            key={id}
            id={id}
            record={data[id]}
            resource={ResourceName.ACCESS_USINGS}
          >
            <DateField
              source="date"
              record={data[id] as AccessUsingDto}
              showTime
            />
            <FunctionField<AccessUsing>
              record={data[id] as AccessUsing}
              render={(record?: AccessUsing) => (
                translate(`resources.enums.deviceDirection.${record?.direction}`)
              )} />
            {!smallScreen &&
              <FunctionField<AccessUsing>
                render={(record?: AccessUsing) =>
                  record?.active
                    ? translate('const.yes')
                    : translate('const.no')
                }
              />}
            <TextField
              source="ticketCode"
            />
            <>
              {smallScreen ?
                <IconButton color="primary" onClick={() =>
                  patchAccess(data[id] as AccessUsingDto)}>
                  {data[id].active ?
                    <Power /> :
                    <PowerOff />}
                </IconButton> :
                <Button
                  variant="contained"
                  color="secondary"
                  style={{ width: '100%', fontSize: '13px' }}
                  onClick={() => patchAccess(data[id] as AccessUsingDto)}
                >
                  {translate(
                    `resources.access-usings.actions.${data[id].active ? `deactivate` : `activate`
                    }`,
                  )}
                </Button>
              }
            </>
          </DatagridRow>
        ))}
    </TableBody>
  )
}

const AccessUsingsHeader = (props: DatagridHeaderProps) => (
  <DatagridHeader {...props}>
    <TextField source="date" />
    <TextField source="direction" />
    <TextField source="ticketId" />
    <TextField source="active" />
  </DatagridHeader>
)

export const AccessUsingsList = (props: ListProps) => {
  const theme = useTheme()
  const xSmallScreen = useMediaQuery(theme.breakpoints.down('xs'))
  return (
    <List
      {...props}
      exporter={false}
      perPage={20}
      sort={{ field: 'date', order: 'DESC' }}
    >
      <>
        <Datagrid
          body={<AccessUsingsBody />}
          header={<AccessUsingsHeader />}
          padding={xSmallScreen ? "checkbox" : "default"}
        >
          <div />
        </Datagrid>
      </>
    </List>
  )
}
