import { DataProvider, GetListResult } from 'react-admin'
import {
  BOOKINGS_URL,
  CUSTOMERS_URL,
  EVENTS_URL,
  TICKETS_URL,
} from '../api-urls'
import { getByConditions, getWithPagination } from '../common/fetch.utils'
import { BookingDto } from '../dto/booking.dto'
import { TicketInfo } from './ticket-info'
import { TicketDto } from '../dto/ticket.dto'
import { CustomerDto } from '../dto/customer.dto'
import { EventDto } from '../dto/event/event.dto'
import { mapToTicketBaseInfoList } from './ticket-info.mapper'
import { buildFilterByIdsCondition } from '../common/condition.builder'
import { TicketCodeFilter } from '../common/ticket-code.filter'
import { toUniqueValues } from '../common/mapper.utils'
import { mapSortTicketParams } from './ticket-info-sort.mapper'
import { getFilterMapper } from './ticket-info-filter.mapper'

const getFilterParams = (
  partialValues: Partial<TicketInfo>,
): string => {
  if (Object.keys(partialValues).length) {
    const filterParams = Object.entries(partialValues).map(([key, value]) => {
      const mapper = getFilterMapper(key as keyof TicketInfo)
      return mapper('t', value as string)
    })
    return filterParams.reduce((p, c) => `${p}&&${c}`)
  }
  return 'true'
}
const getByUniqueIds = <T>(baseUrl: string, ids: number[]): Promise<T[]> => {
  const uniqueIds = toUniqueValues(ids)
  return getByConditions<T>(baseUrl, buildFilterByIdsCondition(uniqueIds))
}

const provider = {
  getList: async (
    resource: string,
    {
      filter,
      pagination: { page, perPage },
      sort: { field, order }
    }: TicketCodeFilter,
  ): Promise<GetListResult<TicketInfo>> => {
    const filterParams = `t=>${getFilterParams(filter)}`
    const queryParams: string[] = []
    if (!!page && !!field) {
      queryParams.push(`sortBy=${mapSortTicketParams(field)}`)
      queryParams.push(`sortOrder=${order}`)
      queryParams.push(`rangeFrom=${page * perPage - perPage}`)
      queryParams.push(`rangeTo=${page * perPage}`)
    }
    else queryParams.push('')

    const pathParams = queryParams.reduce((p, c) => `${p}&${c}`)
    const path = `/${filterParams}?${pathParams}`
    const { data: tickets, range: { total } } = await getWithPagination<TicketDto[]>(
      `${TICKETS_URL}/GetByConditions`, path
    )

    const bookingIds = tickets.map((v) => v.bookingId)
    const bookings = await getByUniqueIds<BookingDto>(BOOKINGS_URL, bookingIds)

    const customerIds = bookings.map((v) => v.customerId)
    const eventIds = bookings.map((v) => v.eventId)

    const pendingCustomers = getByUniqueIds<CustomerDto>(
      CUSTOMERS_URL,
      customerIds,
    )
    const pendingEvents = getByUniqueIds<EventDto>(EVENTS_URL, eventIds)
    const [customers, events] = await Promise.all([
      pendingCustomers,
      pendingEvents,
    ])

    const ticketBaseInfoList = mapToTicketBaseInfoList(
      bookings,
      customers,
      events,
      tickets,
    )
    return Promise.resolve({
      data: ticketBaseInfoList,
      total
    })
  },
} as DataProvider

export default provider
